@import "variables";
@import "functions";
@import "mixins";
@import "sprites";
@import "fonts";
@import "vendor";
@import "base";
@import "helpers";

body {
 background: $gray1;
}

.page-content {
 @extend .d-block;
 margin-top: 250px;
 
 @media(max-width: 1024px) {
   margin-top: 122px;
 }
}
.container {
 margin: 0 auto;
 display: block;
 width: 100%;

 @include desktop {
  max-width: 80%;  
 }
 @include desktop-xl {
  max-width: 1140px;  
 }
 @include tablet {
  padding-left: 20px;
  padding-right: 20px;
 }
 @include mobile {
  padding-left: 20px;
  padding-right: 20px;
 }
}

.scroller__progress {
 display: none;
 width: 48px;
 height: 48px;
 bottom: 25px;
 right: 25px;
 z-index: 100;
 position: fixed;
 border-radius: 50%;
 overflow: hidden;
 background: $red1;

 @media(max-width: 1024px) {
  width: 30px;
  height: 30px;
  bottom: 20px;
  right: 20px; 
 } 

 &::before {
  content: '';
  display: block;
  position: absolute;
  top:2px;
  left:2px;
  right:2px;
  bottom:2px;
  border-radius: 50%;
  background: $gray1;
  z-index: 3;
  opacity: 1;
}
 .piece {
  width: 100%;
  height: 100%;
  left: 0;
  right: 0;
  overflow: hidden;
  position: absolute;

  &::before {
   content: '';
   display: block;
   position: absolute;
   width: 50%;
   height: 100%;
  }

  &.left {
   transform: rotate(0deg);
   z-index: 2;
   border-radius: 50%; 
  }
  &.right {
   transform: rotate(180deg);
   z-index: 1;
   border-radius: 50%; 
  }
 }//- piece

 &.over_50 {
  .piece.left {
   transform: rotate(180deg);
  }
  .piece.right {
   transform: rotate(360deg);
  }

  .left::before {
   background: $red1;
  }  
 }

 .left::before {
  background: $blue1;
 }
 .right::before {
  background: $blue1;
 } 
 .text {
  position: absolute;
  z-index: 3;
  top: 0;
  bottom: 0;
  left:0;
  right:0;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  font: 400 14px/14px $font-sf-pro-display;
  color: $blue1;

  @media(max-width: 1024px) {
   font-size: 10px;
   line-height: 10px;
  }

  b {
   font-weight: 400 !important;
   margin-right: 1px;
  }
 }
}
.btn__scroll-top {
 background: url($img_dir+"icons/svg/icon-btn-scroll.svg") no-repeat center;
 border: 2px solid $red1;
 border-radius: 50%;
 display: none;
 width: 48px;
 height: 48px;
 bottom: 25px;
 right: 25px;
 z-index: 100;
 position: fixed; 

 @media(max-width: 1024px) {
  width: 30px;
  height: 30px;
  bottom: 20px;
  right: 20px; 
  background-size: 10px 12px !important; 
 }
}
@import "components/header";
@import "components/footer";
@import "components/preloader";
@import "components/anime";

@import "pages/home";

