.wrapper {
	@extend .d-block;
}
.candidate {

	&-block {
		position: relative;
	}

	&__content {
		@extend .d-block;
		margin-top: 21px;
	}
	&__info {
		@extend .d-block;
		
		&-content {
			display: flex;
			float: left;
			width: 100%;		
			position: relative;
			z-index: 3;	

			@media(max-width: 1024px) {
				flex-wrap: wrap;
			}
		}

		&-left {
			flex-grow: 1;
			min-width: 1px;
			
			@include desktop {
				padding-right: 36%;
			}			
			@include laptop {
				padding-right: 25%;
			}		
			@media(max-width: 1024px) {
				width: 100%;
				margin-bottom: 20px;
			}	
		}
		&-right {
			flex-shrink: 0;
			width: 100%;
			
			@include desktop {
				width: 24%;
			}
			@include desktop-xl {
				width: 267px;
			}
		}

		&-text {
			@extend .d-block;
		}
		&-contacts {
			@extend .d-block;
		}

		&-contact__list {
			float: left;
			display: flex;
			flex-direction: column;
			width: 100%;
			margin-bottom: 15px;
		
			li {
				font: 400 14px/20px $font-sf-pro-display;
				color: $blue1;

				a {
					font: 400 14px/20px $font-sf-pro-display;
					color: $blue1;
					
					@include hover {
						color: $blue2;
					}
				}
				&:not(:last-child) {
					margin-bottom: 10px;
				}

			}
		}		

		&-contact__social {
			display: flex;
			width: 100%;
			float: left;

			li {

				> span {
					cursor: pointer;
				}

				&:not(:last-child) {
					margin-right: 20px;
				}
			}
		}

	} //- candidate__info

	&__description {
		@extend .d-block;
		position: relative;
		

		&::before {
			@extend .before;
			left: 0;
			top: -96px;
			width: 100%;
			height: 363px;
			background-size: contain !important; 
		}
		
		@include desktop {
			padding-top: 200px;
			
			&::before {
				background: url($img_dir+"line-top.svg") no-repeat top center;
			}
		}
		@media(max-width: 1024px) {
			padding-top: 40px;
			&::before {
				top: -58px;
				height: 123px;
				background: url($img_dir+"line-top-xs.svg") no-repeat top center;
			}
		}		

		
		&-content {
			display: flex;
			float: left;
			width: 100%;		
			position: relative;
			z-index: 2;	

			@media(max-width: 1024px) {
				flex-wrap: wrap;
			}
		}

		&-left {
			flex-grow: 1;
			min-width: 1px;
			
			@include desktop {
				padding-right: 36%;
			}				
			@include laptop {
				padding-right: 25%;
			}			

			@media(max-width: 1024px) {
				width: 100%;
			}
		}
		&-right {
			flex-shrink: 0;
			width: 100%;
			
			@include desktop {
				width: 24%;

				h2 {
					text-align: center
				}
			}
			@include desktop-xl {
				width: 267px;
			}

		}
		&-center {
			@extend .d-block;
			position: relative;

			&::before {
				@extend .before;
				left: 0;
				top: 8px;
				width: 100%;
				height: 363px;
				background-size: contain !important;
			}			


			@include desktop {
				padding-top: 200px;
				
				&::before {
					background: url($img_dir+"line-bottom.svg") no-repeat top center;
				}
			}

			@media(max-width: 1024px) {
				padding-top: 40px;

				&::before {
					top: 0;
					height: 122px;
					background: url($img_dir+"line-bottom-xs.svg") no-repeat top center;
				}
			}

		}

		&-img {
			@include desktop {
				display: none !important;
			}
			@media(max-width: 1024px) {
				display: flex;
				justify-content: center;
				width: 100%;
				float: left;
				align-items: center;
				margin-top: 30px;
				margin-bottom: 28px;

				img {
					max-width: 300px;
				}
			}
		}
	}//- candidate__description
	
	&__experience-list {
		@extend .d-block;

		> li {
			@extend .d-block;

			&:not(:last-child) {
				margin-bottom: 60px;
			}
		}
	}


	&__exp {
		&-date {
			@extend .d-block;
			margin-bottom: 8px;
			font: 400 14px/17px $font-sf-pro-display;
			color: $gray2;
		}
		&-position {
			display: flex;
			align-items: center;
			float: left;
			width: 100%;
			margin-bottom: 8px;
			font: 700 14px/17px $font-sf-pro-display;
			color: blue1;

			&--icon {
				margin-left: 8px;
			}
		}

		&-head {
			display: flex;
			align-items: center;
			width: 100%;
			float: left;
			font: 400 14px/17px $font-sf-pro-display;
			color: blue1;
			margin-bottom: 8px;
		}
		&-logo {
			width: 30px;
			height: 30px;
			flex-shrink: 0;
			margin-right: 8px;

			img {
				border-radius: 50%;
			}
		}
		&-company--name {
			font-weight: 700;
			margin-right: 8px;
		}

		&-company--location {
			white-space: nowrap;
			text-overflow: ellipsis;
			overflow: hidden;
			display: inline-block;
		}


		&-text {
			@extend .d-block;
		}
		&-links {
			@extend .d-block;
			margin-top: 4px;
			
			li {
				@extend .d-block;

				&:not(:last-child) {
					margin-bottom: 3px;
				}

				a {
					font: 400 14px/17px $font-sf-pro-display;
					color: $blue2;
					text-decoration: underline;
					display: inline-block;

					@include hover {
						text-decoration: none;
					}

				}
			}
		}
	}//- __exp

	&__education {
		&-list {
			@extend .d-block;
			
			@include desktop {
				padding: 0 50px;
			}
			@include laptop {
				padding: 0 25px;
			}
			
			li {
				@extend .d-block;
				text-align: center;

				&:not(:last-child) {
					margin-bottom: 30px;
				}
			}

			@media(max-width: 1024px) {
				display: flex;
				flex-wrap: wrap;

				li {
					width: 50%;
					padding: 0 25px;
				}
			}

			@media(max-width: 480px) {
				li {padding: 0 15px;}
			}
		}

		&-icon {
			display: flex;
			float: left;
			width: 100%;
			justify-content: center;
			align-items: flex-start;
			margin-bottom: 5px;
		}
		&-title {
			@extend .d-block;
			margin-bottom: 5px;
		}
		
		&-text {
			@extend .d-block;
		}
	}//- __education
	
	&__skills {

		&-content {
			@extend .d-block;
		}
		&-list {
			
			@include desktop {
				display: flex;
				margin-left: -10px;
				margin-right: -10px;
	
				li {
					padding-left: 10px;
					padding-right: 10px;
					width: 25%;
				}
			}
			@media(max-width: 1024px) {
				display: block;
				width: 100%;
				float: left;

				li {
					display: block;
					width: 100%;
					float: left;

					&:not(:last-child) {
						margin-bottom: 20px;
					}
				}
			}
		}
		&-title {
			@extend .d-block;
			margin-bottom: 6px;
		}
		&-text {
			@extend .d-block;
		}
	}

}

h1 {
	font: 700 36px/50px $font-sf-pro-display;
	color: $blue1;
	margin: 0 0 30px;
	width: 100%;
	float: left;
	
	@media(max-width: 1024px) {
		font-size: 28px;
		line-height: 39px;
		margin-bottom: 20px;
	}
}
h2 {
	font: 700 24px/29px $font-sf-pro-display;
	color: $blue1;
	margin: 0 0 30px;
	width: 100%;
	float: left;

	@media(max-width: 1024px) {
		font-size: 20px;
		line-height: 24px;
		margin-bottom: 20px;
	}	
}
h3 {
	width: 100%;
	float: left;
	font: 700 14px/17px $font-sf-pro-display;
	color: $blue1;
	margin: 0;
}
p {
	font: 400 14px/20px $font-sf-pro-display;
	color: $blue1;
	margin-bottom: 15px;

	&:last-child {
		margin-bottom: 0;
	}
}
.skills-block {
	position: absolute;
	top: -100px;
	left: 0;
}
.candidate__container {
	@extend .d-block;
	position: relative;
}
.user-sticky {
 position: absolute;
 top: 0;
 bottom: 0;
 left: 50%;
	margin-left: 10px;
 visibility: hidden;

	@media(max-width: 1024px) {
		display: none !important;
	}
}
.user-sticky-img {
	position: sticky;
	max-width: 300px;
	top: 120px;
	visibility: visible;
	z-index: 5;
	margin-top: 200px;
}
