#preloader {
 position: fixed;
 left: 0;
 top: 0;
 right: 0;
 bottom: 0;
 z-index: 9999;
 width: 100%;
 height: 100%;
 background: $gray1;
 display: flex;
 justify-content: center;
 align-items: center;

 canvas {
  display: none !important;
 }
}
.progress-percentage {
 transform: translate(-100%,100%);
 -webkit-transform: translate(-100%,100%);
 -o-transform: translate(-100%,100%);
 -moz-transform: translate(-100%,100%);
}

.dws-progress-bar {
 position: absolute;
 transition: all 2s ease-in-out;
 z-index: 100;
}