
.header {
	@extend .d-block;
	position: fixed;
	left: 0;
	right: 0;
	top: 0;
	z-index: 2100;
	background: $gray1;
	height: 250px;	
	
	@include desktop {
		padding-top: 84px;
	}
	
	@media(max-width: 1024px) {
		height: 122px;	
		padding-top: 10px;
	}


	&__logo {
		display: block;
		max-width: 150px;
		margin-top: 16px;
		
		@media(max-width: 1024px) {
			max-width: 80px;
			margin-top: 10px;
		}
		
	}

	&__content {
		display: flex;
		width: 100%;


		&-left {
			flex-shrink: 0;
		}
		&-right {
			display: flex;
			flex-grow: 1;
			min-width: 1px;
			justify-content: flex-end;
		}
	}
	&__nav-list {
		display: flex;

		li {
			display: block;
			
			a {
				display: block;
				padding: 16px;
				position: relative;
				font: 700 18px/21px $font-sf-pro-display;
				color: $blue1;

				&::before {
					@extend .before;
					left: 50%;
					top: -80px;
					opacity: 0;
					transition: opacity 0.25s;
					background: url($img_dir+"active-nav.svg") no-repeat;
					width: 104px;
					height: 156px;
					margin-left: -58px;
				}

				&.is-active {
					&::before {
						opacity: 1;
					}
				}

				span {
					position: relative;
					z-index: 1;
					display: inline-block;
				}

				@include hover {
					&:not(.is-active) {
						color: $blue2;
					}
				}

			}

			&:last-child {
				a {
					padding-right: 0;
				}
			}
		}

		@media(max-width: 1024px) {
			flex-direction: column;

			li {
				width: 100%;

				&:first-child {
					a {
						padding-top: 10px;
					}
				}
				
				a {
					width: 100%;
					font-size: 26px;
					line-height: 31px;
					padding: 20px 40px;

					&::before {
						display: none;
					}

					&.is-active {
						> span::before { opacity: 1; }
					}


					> span {
						&::before {
							content: "";
							display: block;
							position: absolute;
							left: 50%;
							top: -28px;
							opacity: 0;
							transition: opacity 0.25s;
							background: url($img_dir+"active-nav-xs.svg") no-repeat;
							width: 48px;
							height: 70px;
							margin-left: -24px;
						}

						span {
							position: relative;
							z-index: 2;
						}
					}

				}
			}
		}


	}

	&__burger {
		display: none;
		padding: 0;
		margin-right: -20px;
		height: 58px;
		width: 66px;
		justify-content: center;
		align-items: center;
		flex-direction: column;
		background: none;
		box-shadow: 0;
		border: 0;
		
		span {
			width: 26px;
			height: 2.25px;
			background: $blue1;
			display: block;
			transition: 0.4s;
			margin: 2.81px 0;
		}

		@media(max-width: 1024px) {
			display: flex;
		}

		&.is-active {
			span {
				&:nth-child(2) {
					display: none;
				}
				&:first-child {
					transform: translate(0, 3px) rotate(-45deg);
				}
				&:last-child {
					transform: translate(0, -5px) rotate(45deg);
				}
			}
		}
	}
	
	&__nav {
		@media(max-width: 1024px) {
			position: fixed;
			left: 0;
			top: 0;
			z-index: 98;
			overflow: hidden;
			overflow-y: auto;
			width: 100%;
			margin-top: 122px;
			background: $gray1  url($img_dir+"nav-bg-xs.png") no-repeat center bottom 54px;
			background-attachment: fixed;
			opacity: 0;
			transform: translateX(100%);
			transition: 0.4s;
			height: calc(var(--vh) - 122px);

			&.is-active {
				opacity: 1;
				visibility: visible;
				transform: translateX(0);
			}


		}
	}

	&.is-fixed {
		height: 122px;
		padding-top: 20px;
		box-shadow: 0 0 3px 1px rgba($color-black,.2);;

		.header__logo {
			max-width: 80px;
			margin-top: 0;
		}
		.header__content-right {
			padding-top: 13px;
		}

		@media(max-width: 1024px) {
			height: 68px;
			padding-top: 10px;

			.header__content-right {
				padding-top: 0;
			}
			.header__logo {
				max-width: 49px;
			}			

			.header__nav {
				margin-top: 68px;
				height: calc(var(--vh) - 68px);
			}


		}
	}//- is-fixed
}