.anime-title, .anime-text {
 opacity: 0; 
}
.anime-title {
 transform: translateY(50px);  
 transition: all 0.8s cubic-bezier(0.23, 1, 0.32, 1) 0s;
}
.anime-text {
 transform: translateY(70px);  
 transition: all 1s cubic-bezier(0.23, 1, 0.32, 1) 0s;
}

.loading {
 .anime-title, .anime-text {
  opacity: 1;
  transform: translateY(0);
 }
}//- loading