.footer {
	@extend .d-block;
	text-align: center;
	margin: 200px 0 50px;

	&__content {
		display: flex;
		width: 100%;
		justify-content: center;
		
		&-img {
			max-width: 512px;
			
			@media(max-width: 1024px) {
				max-width: 375px;
			}
		}
	}

	@media(max-width: 1024px) {
		margin: 40px 0 40px;
	}
}
