// stylelint-disable scss/dollar-variable-empty-line-before

// COLORS
$color-black: #000;
$color-white: #fff;
$color-gray: #eee;

$gray1: #fafafa;
$gray2: #828282;

$blue1: #282A33;
$blue2: #2F80ED;

$red1: #D2233C;

// FONTS
// stylelint-disable-next-line
$font-primary: -apple-system, blinkmacsystemfont, "Segoe UI", roboto, oxygen, ubuntu, cantarell, "Open Sans", "Helvetica Neue", sans-serif;
$font-sf-pro-display: "SF Pro Display", sans-serif;


// EASING TRANSITIONS
$duration-normal: 0.4s;
$easing-back-out: cubic-bezier(0.25, 0.74, 0.22, 1.1); // http://cubic-bezier.com/#.25,.74,.22,1.1
$easing-back-in: cubic-bezier(0.47, -0.1, 0.5, -0.06); // http://cubic-bezier.com/#.47,-0.1,.5,-.06
$easing-out: ease-out;
$easing-in: ease-in;

// Settings
$desktop: 1025;
$desktop-xl: 1800;
$laptop: 1440;
$tablet: 768;
$mobile: 320;
$desktop-vh: 750;

$img_dir: "../images/";

.before {
	content: "";
	display: block;
	position: absolute;
}
.d-block {
	display: block;
	float: left; 
	width: 100%;    
}
.img-responsive {
 max-width: 100%;
 display: block;
 height: auto;
}